import * as styles from "../../styles/index.module.css";
import * as videoStyles from "../../styles/videoGallery.module.css";
import { StaticImage } from "gatsby-plugin-image";
import * as React from "react";
import { Link } from "gatsby";
import Header from "../../components/header";
import Footer from "../../components/footer";
import SEO from "../../components/SEO";

const videoLinks = [
    { id: "42UMTMEEU34", title: "Walkum Script" },
    { id: "Hcj_Xb7zJfY", title: "Walkum Exploit" },
    { id: "6sEhygfod-Y", title: "Walkum Drive" },
    { id: "-ggV4-u2cL4", title: "Walkum Stack" },
    { id: "WCfR0zcbyMI", title: "Walkum Spawn" },
    { id: "FMpIMnvIKcQ", title: "Walkum Error" },
];

const RecordsPage = () => {
    const [videoLoaded, setVideoLoaded] = React.useState(Array(videoLinks.length).fill(false));

    const handleLoad = (index) => {
        setVideoLoaded((prev) => {
            const updated = [...prev];
            updated[index] = true;
            return updated;
        });
    };

    return (
        <main className={styles.container}>
            <SEO
                title="walkum | records"
                description="Walkum LLC"
                pathname="/records"
            />

            <div className={styles.center}>
                <Header />

                <StaticImage
                    src="../../images/walkum_records.png"
                    alt="walkum picture"
                    className={styles.title_picture}
                />

                <h3>
                    Record production <Link to="./services">services</Link>
                </h3>
                <p>Bundling you with the right musicians</p>

                <br />
                <br />

                <div className={videoStyles.video_gallery}>
                    {videoLinks.map((video, index) => (
                        <div key={index} className={videoStyles.video_wrapper}>
                            {!videoLoaded[index] ? (
                                <img
                                    src={`https://img.youtube.com/vi/${video.id}/hqdefault.jpg`}
                                    alt={`Thumbnail for ${video.title}`}
                                    className={videoStyles.video_thumbnail}
                                />
                            ) : null}
                            <iframe
                                style={{
                                    display: videoLoaded[index] ? "block" : "none",
                                }}
                                width="460"
                                height="300"
                                src={`https://www.youtube.com/embed/${video.id}?controls=0&rel=0&modestbranding=1&loop=1&playlist=${video.id}`}
                                title={`Walkum video ${video.id}`}
                                frameBorder="0"
                                allow="autoplay; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen
                                onLoad={() => handleLoad(index)}
                            ></iframe>
                        </div>
                    ))}
                </div>
            </div>

            <div className={styles.center_text_block}>
                <br />
                <br />
                <br />
                <h1>A note from a <span className={styles.purple}>Walkum studio musician</span>:</h1>
                <br />
                <p className={styles.p_left}>
                    Walkum Records isn't your big-tech answer to music production. If you're trying to be swept up by distribution algorithms, you may find a quicker fix somewhere else for the type of thrill you may be after.
                </p>
                <p className={styles.p_left}>
                    If you're willing to consider that great artistry happens independent of distribution, we'd be thrilled to work with you.
                </p>

                <p className={styles.p_left}>
                    <span className={styles.green}>
                        <strong>
                            Walkum Records is a musical arts catalogue developer
                        </strong>
                    </span>
                    . Our goal is the thrill of the art, not the thrill of a hit
                    song.
                </p>

                <hr />

                <p>Thanks for visiting :) Here is a parting thought:</p>

                <p>
                    "Jazz lifted up improvisation to public awareness and mastered it more than any other art form." -{" "}
                    <Link
                        to="https://youtu.be/6e0wsD-_D3A"
                        target="_blank"
                        rel="noopener noreferrer"
                        >
                        Theo Katzman</Link>
                </p>

                <Footer />
            </div>
        </main>
    );
};

export default RecordsPage;

export const Head = () => (
    <>
        <title>walkum | records</title>
        <meta
            name="description"
            content="walkum llc dave daggett ddaaggeett record records music musician musicians artist gig bass guitar drums studio live jazz producer production sound technician"
        />
    </>
);
